  var countDownDate = new Date("Oct 10, 2023 23:59:59").getTime();

  var countdownTimer = setInterval(function(){
    
    var now = new Date().getTime();
    var difference = countDownDate - now;

    var days = Math.floor(difference/(1000*60*60*24));
    var hours = Math.floor((difference%(1000*60*60*24)) / (1000*60*60));
    var minutes = Math.floor((difference%(1000*60*60)) / (1000*60));
    var seconds = Math.floor((difference%(1000*60)) / 1000);

    

    document.getElementById('dayOnesPlace').innerHTML = (days%10);
    days = Math.floor(days/10);
    document.getElementById('dayTensPlace').innerHTML = (days);

    document.getElementById('hourOnesPlace').innerHTML = (hours%10);
    hours = Math.floor(hours/10);
    document.getElementById('hourTensPlace').innerHTML = (hours);

    document.getElementById('minOnesPlace').innerHTML = (minutes%10);
    minutes = Math.floor(minutes/10);
    document.getElementById('minTensPlace').innerHTML = (minutes);

    document.getElementById('secOnesPlace').innerHTML = (seconds%10);
    seconds = Math.floor(seconds/10);
    document.getElementById('secTensPlace').innerHTML = (seconds);
    if(difference == 0)clearInterval(countdownTimer);
  },1000);
 
  $('img').attr('draggable', false);


  var canvas = document.getElementById('matrix'),
    context = canvas.getContext( '2d' );
context.globalCompositeOperation = 'lighter';
canvas.width = 1280;
canvas.height = 800;
draw();

var textStrip = ['0', '1', '0', '0', '0', '0', '1', '0', '1', '0', '1', '1', '0'];

var stripCount = 30, stripX = new Array(), stripY = new Array(), dY = new Array(), stripFontSize = new Array();

for (var i = 0; i < stripCount; i++) {
    stripX[i] = Math.floor(Math.random()*1265);
    stripY[i] = -100;
    dY[i] = Math.floor(Math.random()*7)+3;
    stripFontSize[i] = Math.floor(Math.random()*10)+7;
}

var theColors = ['#fbe6ce', '#ecba72', '#d69046', '#d19a3c', '#d19a3c', '#d19a3c'];

var elem, context, timer;

function drawStrip(x, y) {
    for (var k = 0; k <= 20; k++) {
        var randChar = textStrip[Math.floor(Math.random()*textStrip.length)];
        if (context.fillText) {
            switch (k) {
            case 0:
                context.fillStyle = theColors[0]; break;
            case 1:
                context.fillStyle = theColors[1]; break;
            case 3:
                context.fillStyle = theColors[2]; break;
            case 7:
                context.fillStyle = theColors[3]; break;
            case 13:
                context.fillStyle = theColors[4]; break;
            case 17:
                context.fillStyle = theColors[5]; break;
            }
            context.fillText(randChar, x, y);
        }
        y -= stripFontSize[k];
    }
}

function draw() {
    // clear the canvas and set the properties
    context.clearRect(0, 0, canvas.width, canvas.height);
    context.shadowOffsetX = context.shadowOffsetY = 0;
    context.shadowBlur = 8;
    // context.shadowColor = '#94f475';
    context.shadowColor = '#ecba72';
    
    for (var j = 0; j < stripCount; j++) {
        context.font = stripFontSize[j]+'px MatrixCode';
        context.textBaseline = 'top';
        context.textAlign = 'center';
        
        if (stripY[j] > 1358) {
            stripX[j] = Math.floor(Math.random()*canvas.width);
            stripY[j] = -100;
            dY[j] = Math.floor(Math.random()*7)+3;
            stripFontSize[j] = Math.floor(Math.random()*10)+7;
            drawStrip(stripX[j], stripY[j]);
        } else drawStrip(stripX[j], stripY[j]);
        
        stripY[j] += dY[j];
    }
  setTimeout(draw, 70);
}

